* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  height: 100%;
}
body {
  min-height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: cursive;
  background-color: #03045E;
  color: #FFF;
}
.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
h1 {
  text-align: center;
  font-size: 3.5rem;
  margin-bottom: 1.5rem;
}
main {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.board {
  display: grid;
  width: 22rem;
  height: 22rem;
  grid-template-columns: 7rem 7rem 7rem;
  grid-template-rows: 7rem 7rem 7rem;
  justify-content: center;
}
.board :nth-child(odd) {
  border: none;
}
.board :nth-child(4), .board :nth-child(6){
  border-right: none;
  border-left: none;
}
.board :nth-child(5) {
  border: 2px solid #FFF;
}
.board :nth-child(2), .board :nth-child(8){
  border-top: none;
  border-bottom: none;
}
.board label {
  font-size: 4.5rem;
  padding-top: 0.3rem;
}
label {
  background-color: #03045E;
  border: 2px solid #FFF;
  text-align: center;
}
.control {
  width: 21rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1rem;
}
.report, .reset {
  width: 45%;
  padding: 0.5rem 1.5rem;
}

.reset {
  background-color: #03045E;
  border: 2px solid #FFF;
  align-self: stretch;
  color: #FFF;
  font-size: 1rem;
}
.reset:hover {
  background-color: #FFF;
  color: #03045E;
  transition: all ease-in-out 200ms;
  font-size: 1.1rem;
}

